.header {
  overflow: hidden;
  background-color: #f1f1f1;
  padding: 20px 10px;
}

.logo {
  float: left;
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
  font-size: 25px;
  font-weight: bold;
}

.logo:hover {
  background-color: #ddd;
  color: black;
}

.logo:active {
  background-color: dodgerblue;
  color: white;
}

.login {
  float: right;
}
